:root {
  --wide-container: 60rem;
  --narrow-container: 40rem;

  --font-family-sans: -apple-system, BlinkMacSystemFont, sans-serif;

  --color-black: #202123;
  --color-dark-gray: #32373e;
  --color-gray: #697a90;
  --color-light-gray: #b4bcc7;
  --color-very-light-gray: #e7ebed;
  --color-white: #fff;
  --color-accent: #156dff;

  /* Typography */
  --unit: 16;
  --font-micro-size: calc(10 / var(--unit) * 1rem); /* 10px */
  --font-micro-line-height: calc(12 / 10); /* 12px */
  --font-small-size: calc(14 / var(--unit) * 1rem); /* 14px */
  --font-small-line-height: calc(21 / 14); /* 21px */
  --font-base-size: 1em; /* 16px */
  --font-base-line-height: calc(24 / var(--unit)); /* 24px */
  --font-large-size: calc(18 / var(--unit) * 1rem); /* 18px */
  --font-large-line-height: calc(27 / 18); /* 27px */

  --font-title3-size: calc(21 / var(--unit) * 1rem); /* 21px */
  --font-title3-line-height: calc(30 / 21); /* 30px */
  --font-title2-size: calc(24 / var(--unit) * 1rem); /* 24px */
  --font-title2-line-height: calc(33 / 24); /* 33px */
  --font-title1-size: calc(49 / var(--unit) * 1rem); /* 49px */
  --font-title1-line-height: calc(57 / 49); /* 57px */

  /* Widths */
  --width-x-small: 20em;
  --width-small: 40em;
  --width-medium: 60em;
  --width-large: 80em;
  --width-x-large: 100em;
}
.root {
  padding: 2rem 0
}
@media (min-width: 675px) {
.root {
    padding: 4rem 0
}
  }
.container {
  width: 100%;
  max-width: 40em;
  max-width: var(--width-small);
  padding: 0 1.5em;
  box-sizing: border-box;
  margin: 0 auto;
}
.heading {
  text-align: center;
  font-size: calc(21 / 16 * 1rem);
  font-size: var(--font-title3-size);
  line-height: calc(30 / 21);
  line-height: var(--font-title3-line-height);
  margin: 0 0 0.5rem
}
@media (min-width: 675px) {
.heading {
    font-size: calc(24 / 16 * 1rem);
    font-size: var(--font-title2-size);
    line-height: calc(33 / 24);
    line-height: var(--font-title2-line-height)
}
  }
.subtitle {
  text-align: center;
  margin: 0.5rem 0 1.5rem;
}
.form {
  display: grid
}
@media (min-width: 675px) {
.form {
    min-width: 34rem;
    display: grid;
    grid-template-columns: 80% 20%
}
  }
.form input {
  -webkit-font-smoothing: inherit;
  font: inherit;
  display: block;
  border: 1px solid #202123;
  border: 1px solid var(--color-black, #000);
  -webkit-box-flex: 1;
          flex-grow: 1;
  padding: 1rem;
  width: 100%;
  max-width: 100%;
  border-radius: 0;
  box-sizing: border-box
}
@media (min-width: 675px) {
.form input {
    padding: 1em
}
  }
.form button {
  -webkit-font-smoothing: inherit;
  font: inherit;
  background: none;
  margin-top: 1rem;
  display: block;
  padding: 1em;
  border: 1px solid #202123;
  border: 1px solid var(--color-black, #000)
}
@media (min-width: 675px) {
.form button {
    margin-top: 0;
    border-left: none
}
  }
.form button:focus {
    background: #202123;
    background: var(--color-black, #000);
    color: #fff;
    color: var(--color-white, #fff)
}
@media (hover: hover) {
.form button:hover {
      background: #202123;
      background: var(--color-black, #000);
      color: #fff;
      color: var(--color-white, #fff)
}
  }
