:root {
  --wide-container: 60rem;
  --narrow-container: 40rem;

  --font-family-sans: -apple-system, BlinkMacSystemFont, sans-serif;

  --color-black: #202123;
  --color-dark-gray: #32373e;
  --color-gray: #697a90;
  --color-light-gray: #b4bcc7;
  --color-very-light-gray: #e7ebed;
  --color-white: #fff;
  --color-accent: #156dff;

  /* Typography */
  --unit: 16;
  --font-micro-size: calc(10 / var(--unit) * 1rem); /* 10px */
  --font-micro-line-height: calc(12 / 10); /* 12px */
  --font-small-size: calc(14 / var(--unit) * 1rem); /* 14px */
  --font-small-line-height: calc(21 / 14); /* 21px */
  --font-base-size: 1em; /* 16px */
  --font-base-line-height: calc(24 / var(--unit)); /* 24px */
  --font-large-size: calc(18 / var(--unit) * 1rem); /* 18px */
  --font-large-line-height: calc(27 / 18); /* 27px */

  --font-title3-size: calc(21 / var(--unit) * 1rem); /* 21px */
  --font-title3-line-height: calc(30 / 21); /* 30px */
  --font-title2-size: calc(24 / var(--unit) * 1rem); /* 24px */
  --font-title2-line-height: calc(33 / 24); /* 33px */
  --font-title1-size: calc(49 / var(--unit) * 1rem); /* 49px */
  --font-title1-line-height: calc(57 / 49); /* 57px */

  /* Widths */
  --width-x-small: 20em;
  --width-small: 40em;
  --width-medium: 60em;
  --width-large: 80em;
  --width-x-large: 100em;
}
.root {
  position: relative;
  padding: 2rem 0
}
@media (min-width: 675px) {
.root {
    padding: 4rem 0
}
  }
.label {
  font-size: calc(10 / 16 * 1rem);
  font-size: var(--font-micro-size);
  line-height: calc(12 / 10);
  line-height: var(--font-micro-line-height);
  letter-spacing: 0.5px;
  text-transform: uppercase;
  margin-top: 1em;
}
.label + .title {
  margin-top: 0.2em;
}
.title {
  font-size: calc(21 / 16 * 1rem);
  font-size: var(--font-title3-size);
  line-height: calc(30 / 21);
  line-height: var(--font-title3-line-height);
}
.image {
  display: block;
  width: 100%;
}
.content {
  position: relative;
  margin: 0 1.5rem;
  max-width: 60em;
  max-width: var(--width-medium)
}
.content figcaption {
    width: 100%;
    box-sizing: border-box
}
@media (min-width: 675px) {
.content {
    margin: 0 auto;
    padding-top: 2rem
}
.content figcaption {
      position: absolute;
      top: 0
}
  }
.caption {
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box
}
@media (min-width: 675px) {
.caption {
    max-width: calc(40em - 3rem);
    max-width: calc(var(--width-small) - 3rem)
}
  }
.captionBox {
  background-color: #fff;
  background-color: var(--color-white);
  border: 1px solid #202123;
  border: 1px solid var(--color-black);
  padding: 1.5rem
}
@media (min-width: 675px) {
.captionBox {
    max-width: calc((40em / 2) - 3rem);
    max-width: calc((var(--width-small) / 2) - 3rem)
}
  }
