:root {
  --wide-container: 60rem;
  --narrow-container: 40rem;

  --font-family-sans: -apple-system, BlinkMacSystemFont, sans-serif;

  --color-black: #202123;
  --color-dark-gray: #32373e;
  --color-gray: #697a90;
  --color-light-gray: #b4bcc7;
  --color-very-light-gray: #e7ebed;
  --color-white: #fff;
  --color-accent: #156dff;

  /* Typography */
  --unit: 16;
  --font-micro-size: calc(10 / var(--unit) * 1rem); /* 10px */
  --font-micro-line-height: calc(12 / 10); /* 12px */
  --font-small-size: calc(14 / var(--unit) * 1rem); /* 14px */
  --font-small-line-height: calc(21 / 14); /* 21px */
  --font-base-size: 1em; /* 16px */
  --font-base-line-height: calc(24 / var(--unit)); /* 24px */
  --font-large-size: calc(18 / var(--unit) * 1rem); /* 18px */
  --font-large-line-height: calc(27 / 18); /* 27px */

  --font-title3-size: calc(21 / var(--unit) * 1rem); /* 21px */
  --font-title3-line-height: calc(30 / 21); /* 30px */
  --font-title2-size: calc(24 / var(--unit) * 1rem); /* 24px */
  --font-title2-line-height: calc(33 / 24); /* 33px */
  --font-title1-size: calc(49 / var(--unit) * 1rem); /* 49px */
  --font-title1-line-height: calc(57 / 49); /* 57px */

  /* Widths */
  --width-x-small: 20em;
  --width-small: 40em;
  --width-medium: 60em;
  --width-large: 80em;
  --width-x-large: 100em;
}
.root {
  position: relative;
  color: #202123;
  color: var(--color-black, #000);
  display: -webkit-box;
  display: flex;
  margin: 0 auto;
  max-width: 40em;
  max-width: var(--width-small);
}
.root a {
  color: inherit;
  text-decoration: inherit;
}
.branding {
  font-size: inherit;
  font-weight: inherit;
  text-transform: uppercase;
  margin: 0;
  padding: 0
}
.branding > a {
    display: block;
    padding: 1.5em
}
@supports (padding-top: env(safe-area-inset-top)) {
.branding > a {
      padding-left: calc(env(safe-area-inset-left) + 1.5rem)
}
    }
.branding img, .branding svg {
    height: calc(calc(24 / 16) * 1.5rem);
    height: calc(var(--font-base-line-height) * 1.5rem);
    width: auto;
    color: inherit;
    vertical-align: top
}
.branding svg path {
    fill: currentColor
}
@media (max-width: 674px) {
.branding {
    -webkit-box-flex: 1;
            flex: 1
}
  }
.logo {
  display: block;
}
@media (min-width: 675px) {
.nav {
    -webkit-box-flex: 1;
            flex: 1
}
  }
.navItems {
  padding: 0;
  margin: 0
}
@media (min-width: 675px) {
.navItems {
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: end;
            justify-content: flex-end;
    padding: 0 1rem
}
  }
@supports (padding-top: env(safe-area-inset-top)) {
.navItems {
    padding-right: calc(env(safe-area-inset-right) + 1rem)
}
  }
@media (max-width: 674px) {
.navItems {
    z-index: 10;
    display: none;
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    background-color: #202123;
    background-color: var(--color-black, #000);
    color: #fff;
    color: var(--color-white, #fff)
}
.root[data-show-nav="true"] .navItems {
      display: block
}
  }
.navItem {
  display: -webkit-box;
  display: flex;
  white-space: nowrap;
  -webkit-box-align: stretch;
          align-items: stretch;
  line-height: calc(calc(24 / 16) * 1.5rem);
  line-height: calc(var(--font-base-line-height) * 1.5rem)
}
.navItem > a {
    display: block;
    position: relative;
    padding: 1.5rem 0.5rem
}
@media (max-width: 674px) {
.navItem > a {
      padding: 1rem 1.5rem;
      width: 100%;
      box-sizing: border-box;
      text-align: right
}
.navItem > a[data-is-active="true"]::before {
        content: "";
        width: 100%;
        height: 1px;
        position: absolute;
        bottom: 0.5rem;
        left: 0;
        background-color: #fff;
        background-color: var(--color-white, #fff)
}
    }
.root[data-show-nav="true"] .navItem > a[data-is-active="true"]::before {
    display: none
}
@media (min-width: 675px) {
.navItem {
    margin-left: 0.5rem
}
  }
.showNavButton {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  font: inherit;
  background: none;
  color: inherit;
  margin: 0;
  padding: 1.5rem;
  outline: none
}
@media (min-width: 675px) {
.showNavButton {
    display: none
}
  }
.hamburgerIcon {
  font-size: calc(calc(24 / 16) * 1.5rem);
  font-size: calc(var(--font-base-line-height) * 1.5rem);
  display: block
}
@media (min-width: 675px) {
.hamburgerIcon {
    display: none
}
  }
