:root {
  --wide-container: 60rem;
  --narrow-container: 40rem;

  --font-family-sans: -apple-system, BlinkMacSystemFont, sans-serif;

  --color-black: #202123;
  --color-dark-gray: #32373e;
  --color-gray: #697a90;
  --color-light-gray: #b4bcc7;
  --color-very-light-gray: #e7ebed;
  --color-white: #fff;
  --color-accent: #156dff;

  /* Typography */
  --unit: 16;
  --font-micro-size: calc(10 / var(--unit) * 1rem); /* 10px */
  --font-micro-line-height: calc(12 / 10); /* 12px */
  --font-small-size: calc(14 / var(--unit) * 1rem); /* 14px */
  --font-small-line-height: calc(21 / 14); /* 21px */
  --font-base-size: 1em; /* 16px */
  --font-base-line-height: calc(24 / var(--unit)); /* 24px */
  --font-large-size: calc(18 / var(--unit) * 1rem); /* 18px */
  --font-large-line-height: calc(27 / 18); /* 27px */

  --font-title3-size: calc(21 / var(--unit) * 1rem); /* 21px */
  --font-title3-line-height: calc(30 / 21); /* 30px */
  --font-title2-size: calc(24 / var(--unit) * 1rem); /* 24px */
  --font-title2-line-height: calc(33 / 24); /* 33px */
  --font-title1-size: calc(49 / var(--unit) * 1rem); /* 49px */
  --font-title1-line-height: calc(57 / 49); /* 57px */

  /* Widths */
  --width-x-small: 20em;
  --width-small: 40em;
  --width-medium: 60em;
  --width-large: 80em;
  --width-x-large: 100em;
}
.root {
  composes: center from '../../styles/shared.module.css';
  position: relative;
  background-color: #202123;
  background-color: var(--color-black, #000);
  color: #fff;
  color: var(--color-white, #fff);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  padding-bottom: 2rem
}
@media (min-width: 675px) {
.root {
    padding-bottom: 4rem
}
  }
.root::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(34,34,34,0)), to(rgba(34,34,34,0.93333)));
  background-image: linear-gradient(rgba(34,34,34,0), rgba(34,34,34,0.93333));
  width: 100%;
  height: 50%;
  z-index: 0;
}
.content {
  width: 100%;
  max-width: 40em;
  max-width: var(--width-small);
  padding: 0 1.5em;
  box-sizing: border-box;
  z-index: 1;
}
.title {
  position: relative;
  font-weight: 600;
  font-size: calc(24 / 16 * 1rem);
  font-size: var(--font-title2-size);
  line-height: calc(33 / 24);
  line-height: var(--font-title2-line-height);
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  margin: 0;
  padding: 0;
  padding-top: 12.5rem
}
@media (min-width: 675px) {
.title {
    font-size: calc(49 / 16 * 1rem);
    font-size: var(--font-title1-size);
    line-height: calc(57 / 49);
    line-height: var(--font-title1-line-height);
    padding-top: 15rem
}
  }
.tagline {
  position: relative;
  margin: 0;
  padding: 0;
  margin-top: 0.5em;
  margin-bottom: 1rem;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5)
}
@media (min-width: 675px) {
.tagline {
    font-size: calc(18 / 16 * 1rem);
    font-size: var(--font-large-size);
    line-height: calc(27 / 18);
    line-height: var(--font-large-line-height)
}
  }
.tagline > p {
  margin: 0;
}
.ctas {
  margin-top: 3rem;
  display: -webkit-box;
  display: flex;
}
.ctas > *:not(:first-child) {
  margin-left: 1rem;
}
.root p a {
  color: inherit;
}
